.videoContainer{
    width: 100vw;
    
}
.imgContainer{
    width: 100vw;
    display: flex;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 40px;
}

.textoContainer{
    margin-top: 40px;
    width: 100vw;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.textoContainer h3{
    font-size: 64px;
    font-weight: bold;
    margin-bottom: 40px;
}

.textoContainer p{
    width: 80%;
    font-size: 24px;
    line-height: 40px;
    margin-bottom: 60px;
}


@media (max-width: 765px) {


    .imgContainer img{

        width: 360px;
        
    }

    .textoContainer h3{
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 40px;
    }
    
    .textoContainer p{
        width: 80%;
        font-size: 14px;
        line-height: 40px;
        margin-bottom: 60px;
    }
}